.projects {
    background-color: #252527;
    padding: 100px 0;
    position: relative;
}

.projectsBlock {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

@media (max-width: 767px) {
    .projects {
        padding: 50px 0;
    }
}