body {
    margin: 0;
    padding: 0;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 1px;
    color: #ffffff;
    font-weight: 400;
    background-color: #1F1F20;
    font-family: 'Poppins', 'Arial', sans-serif;
}

img {
    max-width: 100%;
    height: auto;
}

.container {
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;
}

@media (min-width: 360px) {
    .container {
        padding-left: 10px;
        padding-right: 10px;
        width: 100%;
        box-sizing: border-box;
    }
}

@media (min-width: 768px) {
    .container {
        width: 738px;
    }
}

@media (min-width: 1024px) {
    .container {
        width: 994px;
    }
}

@media (min-width: 1200px) {
    .container {
        width: 1170px;
    }
}


