.title {
    margin: 0;
    margin-bottom: 80px;
    position: relative;
    font-size: 30px;
    line-height: 32px;
    font-weight: 400;
    letter-spacing: 1px;
    text-align: center;
}

.title::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50px;
    margin-left: -30px;
    background: #1D6CFF;
    width: 60px;
    height: 2px;
}

@media (max-width: 767px) {
    .title {
        font-size: 24px;
        line-height: 32px;
        margin-bottom: 60px;
    }
}

.button {
    display: block;
    text-decoration: none;
    letter-spacing: 1px;
    padding: 7px 20px;
    cursor: pointer;
    -webkit-transition: all .4s ease-in-out 0s;
    transition: all .4s ease-in-out 0s;
    background: transparent;
    color: #fff;
    z-index: 0;
    text-align: center;
    border-radius: 0;
    border: none;
    position: relative;
    font-size: 18px;
    line-height: 20px;
    font-weight: 400;
}

.button::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 10px;
    width: 100%;
    background: #1D6CFF;
    z-index: -1;
    -webkit-transition: all .2s ease-in-out 0s;
    transition: all .2s ease-in-out 0s;
    margin: 0 auto;
}

.button:hover,
.button:focus {
    box-shadow: 0 10px 20px rgba(0, 0, 0, .1);
    color: #fff;
    outline: none;
}

.button:hover::after {
    height: 100%;
    width: 100%;
    -webkit-transition: .5s;
    transition: .5s;
}